import React, { Component } from 'react';
import { Button } from 'reactstrap';
import PropTypes from 'prop-types';

import Logo from 'assets/images/common/logo.svg';

import styles from './Splash.module.scss';

class Splash extends Component {
  render() {
    const { title } = this.props;

    return (
      <div className={[styles.splashContainer, 'main-margin'].join(' ')}>
        <div className={styles.logoContainer}>
          <img className={styles.mainLogo} src={Logo} alt="INFINTIE FRONTIER" />
          <h1>INFINITE FRONTIER</h1>
        </div>
        <h2>About Us</h2>
        <hr />
        <p>We provide a one stop solution to you digital marketing needs. Finally, you can build your branding and increase sales without burning a huge budget.</p>
        <div className={styles.buttonContainer}>
          <Button
            onClick={() => {
              try {
                
                console.log(document.getElementsByTagName("html"));
                console.log(document.getElementById('facebook').children);
                console.log(document.getElementById('facebook').children[1].children);
                document.getElementById('facebook').children[1].children[0].click();
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
                console.log(error);
              }
            }}
          >
            <span>ENQUIRE NOW!</span>
            <i className="fas fa-arrow-right" />
          </Button>
        </div>
      </div>
    );
  }
}

Splash.defaultProps = {
  title: '',
};

Splash.propTypes = {
  title: PropTypes.string,
};

export default Splash;
